import { request, requestDownload } from 'utils';

const prefix = '/api/students';

export const getStudents = dataSearch => {
  console.log('dataSearch', dataSearch);
  let statisticType = 'Course';
  if (dataSearch.yearEduId && !dataSearch.semesterId) {
    statisticType = 'YearEdu';
  } else if (dataSearch.yearEduId && dataSearch.semesterId) {
    statisticType = 'Semester';
  }
  return request({
    prefix,
    url: `/TrainingScore/GetTrainingScoreStatistic`,
    params: { ...dataSearch, statisticType },
    method: 'GET',
  });
};

export const getTrainingScoreStatisticSummary = dataSearch => {
  console.log('dataSearch', dataSearch);
  let statisticType = 'Course';
  if (dataSearch.yearEduId && !dataSearch.semesterId) {
    statisticType = 'YearEdu';
  } else if (dataSearch.yearEduId && dataSearch.semesterId) {
    statisticType = 'Semester';
  }
  return request({
    prefix,
    url: `/TrainingScore/GetTrainingScoreStatisticSummary`,
    params: { ...dataSearch, statisticType },
    method: 'GET',
  });
};

export const downloadFile = dataSearch => {
  console.log('dataSearch', dataSearch);
  let statisticType = 'Course';
  if (dataSearch.yearEduId && !dataSearch.semesterId) {
    statisticType = 'YearEdu';
  } else if (dataSearch.yearEduId && dataSearch.semesterId) {
    statisticType = 'Semester';
  }
  return requestDownload({
    prefix,
    defaultFileName: 'downloadFile.xlsx',
    url: '/TrainingScore/ExportTrainingScoreStatistic',
    method: 'POST',
    data: {
      ...dataSearch,
      statisticType,
    },
  });
};



